nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  padding: 0.7rem 1.7rem;
  position: fixed;
  margin: auto;
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
  z-index: 2;
  left:0;
  right:0;
}

nav a {
  background: transparent;
  display: flex;
  color: var(--color-light);
  border-radius: 50%;
  font-size: 1.1rem;
  padding: 0.9rem;
}

nav a:hover {
  background: rgb(0, 0, 0, 0.3)
}

nav a.active{
  background:var(--color-primary);
  color:var(--color-bg);
}